.menuActive {
    width: 492px;  
    
}

.menuDisable {
    width: 0px;
    overflow: hidden;
    
}

.navToggleActive {
    background: red;
    
}

.test::before {
    content:"";
        position: absolute;
        width: 28px;
        height: 2px;
        background: #fff;
        transform: translateY(-5px);
        transition: 0.2s;
}

.test::after {
    content:"";
        position: absolute;
        width: 28px;
        height: 2px;
        background: #fff;
        transform: translateY(5px);
        transition: 0.2s;
}
.navToggleActive::before {
    transform: translateY(0px) rotate(45deg);
    
}
.navToggleActive::after {
    transform: translateY(0px) rotate(-45deg);
    
}

.navToggleDisable {
    background: #9051ff; 
}